import React, {useState} from 'react';
import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardHeader from '@material-ui/core/CardHeader';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import StarIcon from '@material-ui/icons/StarBorder';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Box from '@material-ui/core/Box';
import TextField from '@material-ui/core/TextField';
import { csrfRequest } from "../utils/csrfRequest";

function Copyright() {
    return (
        <Typography variant="body2" color="textSecondary" align="center">
            {'Copyright © '}
            <Link color="inherit">
                DireitoDigital
            </Link>{' '}
            {new Date().getFullYear()}
            {'.'}
        </Typography>
    );
}

const useStyles = makeStyles((theme) => ({
    '@global': {
        ul: {
            margin: 0,
            padding: 0,
            listStyle: 'none',
        },
    },
    appBar: {
        background: "linear-gradient(#5D2DFD 40%, #7F5DF0);" ,
        color: 'white',
        borderBottom: `1px solid ${theme.palette.divider}`,
    },
    toolbar: {
        flexWrap: 'wrap',
    },
    toolbarTitle: {
        flexGrow: 1,
    },
    link: {
        margin: theme.spacing(1, 1.5),
    },
    heroContent: {
        padding: theme.spacing(8, 0, 6),
    },
    cardHeader: {
        backgroundColor:
            theme.palette.type === 'light' ? theme.palette.grey[200] : theme.palette.grey[700],
    },
    cardPricing: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'baseline',
        marginBottom: theme.spacing(2),
    },
    footer: {
        borderTop: `1px solid ${theme.palette.divider}`,
        marginTop: theme.spacing(8),
        paddingTop: theme.spacing(3),
        paddingBottom: theme.spacing(3),
        [theme.breakpoints.up('sm')]: {
            paddingTop: theme.spacing(6),
            paddingBottom: theme.spacing(6),
        },
    },
}));

const tiers = [
    {
        title: '6 Meses',
        code: 'PAYPAL_6_MONTH_PLAN',
        price: '18',
        description: ['Plano Basico',],
        buttonText: 'Ativa já a tua aplicaçao',
        buttonVariant: 'outlined',
        redirect_url: '/one-month-offer/'
    },
    {
        title: '3 Meses',
        code: 'PAYPAL_3_MONTH_PLAN',
        subheader: 'Mais Popular',
        price: '9',
        description: [
            'Plano Mais Popular',
            'Suporte por Email prioritario',
        ],
        buttonText: 'Torna-te Premium',
        buttonVariant: 'contained',
        redirect_url: '/three-month-offer/'
    },
    {
        title: '12 Meses',
        code: 'PAYPAL_12_MONTH_PLAN',
        price: '36',
        description: [
            'Suporte Prioritario',
        ],
        buttonText: 'Recebe já o teu codigo',
        buttonVariant: 'outlined',
        redirect_url: '/one-year-offer/'
    },
];
const footers = [
    {
        title: 'Company',
        description: ['Team', 'History', 'Contact us', 'Locations'],
    },
    {
        title: 'Features',
        description: ['Cool stuff', 'Random feature', 'Team feature', 'Developer stuff', 'Another one'],
    },
    {
        title: 'Resources',
        description: ['Resource', 'Resource name', 'Another resource', 'Final resource'],
    },
    {
        title: 'Legal',
        description: ['Privacy policy', 'Terms of use'],
    },
];

export default function Pricing() {
    const classes = useStyles();

    const [deviceId, setDeviceId] = useState(null)

    const handlePaymentButtonCallback = (response, status) => {
        if (response.redirect_url) {
            window.location.href = response.redirect_url;
        }
        console.log(response.message)
    }

    const handlePaymentButton = (planCode) => {
        csrfRequest('POST', `${window.location.href}api/handle-paypal-redirect/`, handlePaymentButtonCallback, {device_id: deviceId, plan_code: planCode})
    }

    return (
        <React.Fragment>
            <CssBaseline />
            <AppBar position="static" color="dark" elevation={0} className={classes.appBar}>
                <Toolbar className={classes.toolbar}>
                    <Typography variant="h6" color="inherit" noWrap className={classes.toolbarTitle}>
                        Direito Digital
                    </Typography>
                </Toolbar>
            </AppBar>
            {/* Hero unit */}
            <Container maxWidth="sm" component="main" className={classes.heroContent}>
                <Typography component="h1" variant="h2" align="center" color="textPrimary" gutterBottom>
                    Preços
                </Typography>
                <Typography variant="h5" align="center" color="textSecondary" component="p">
                    Torna-te já um utilizador Premium e disfruta da nossa aplicaçao por completo.
                </Typography>
            </Container>
            {/* End hero unit */}
            <Container maxWidth="md" component="main">
                <Grid container spacing={5} alignItems="flex-end">
                    {tiers.map((tier) => (
                        // Enterprise card is full width at sm breakpoint
                        <Grid item key={tier.title} xs={12} sm={tier.title === 'Enterprise' ? 12 : 6} md={4}>
                            <Card>
                                <CardHeader
                                    title={tier.title}
                                    subheader={tier.subheader}
                                    titleTypographyProps={{ align: 'center' }}
                                    subheaderTypographyProps={{ align: 'center' }}
                                    action={tier.title === '3 Meses' ? <StarIcon style={{color: "#cba20a"}} /> : null}
                                    className={classes.cardHeader}
                                />
                                <CardContent>
                                    <div className={classes.cardPricing}>
                                        <Typography component="h2" variant="h3" color="textPrimary">
                                            {tier.price}€
                                        </Typography>
                                    </div>
                                    <ul>
                                        {tier.description.map((line) => (
                                            <Typography component="li" variant="subtitle1" align="center" key={line}>
                                                {line}
                                            </Typography>
                                        ))}
                                    </ul>
                                </CardContent>
                                <CardActions>
                                        <Button onClick={() => handlePaymentButton(tier.code)} fullWidth variant={tier.buttonVariant} color="primary">
                                                {tier.buttonText}
                                        </Button>
                                </CardActions>
                            </Card>
                        </Grid>
                    ))}
                    <TextField onChange={(event) => setDeviceId(event.target.value)} label="Device Public ID" variant="outlined" style={{width: "100%", marginTop: 20}}></TextField>
                </Grid>
            </Container>
            {/* Footer */}
            <Container maxWidth="md" component="footer" className={classes.footer}>
                <Grid container spacing={4} justifyContent="space-evenly">
                    {/*footers.map((footer) => (
                        <Grid item xs={6} sm={3} key={footer.title}>
                            <Typography variant="h6" color="textPrimary" gutterBottom>
                                {footer.title}
                            </Typography>
                            <ul>
                                {footer.description.map((item) => (
                                    <li key={item}>
                                        <Link href="#" variant="subtitle1" color="textSecondary">
                                            {item}
                                        </Link>
                                    </li>
                                ))}
                            </ul>
                        </Grid>
                    ))*/}
                </Grid>
                <Box mt={5}>
                    <Copyright />
                </Box>
            </Container>
            {/* End footer */}
        </React.Fragment>
    );
}